import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Tipo_turno } from '../../../modelo/Tipo_turno'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'tipo_turno-dados-details',   templateUrl: 'tipo_turno_dadosDetails.component.html' }) export class Tipo_turnoDadosDetailsComponent implements OnInit {    tipo_turno: Tipo_turno;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.tipo_turno.Tipo_turno";                 let id = +params['id'];         this.tipo_turno = new Tipo_turno();         this.tipo_turno.cd_tipo_turno = id;         o.objetoJson = this.tipo_turno;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a tipo_turno. '; }      this.tipo_turno = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


