import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'tipo_turno-dados',   templateUrl: 'tipo_turno_dados.component.html' }) export class Tipo_turnoDadosComponent implements OnInit {       displayMode: Tipo_turnoDadosDisplayModeEnum;     displayModeEnum = Tipo_turnoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = Tipo_turnoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = Tipo_turnoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = Tipo_turnoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum Tipo_turnoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


